body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
}



.Header{
    height: 20vh;
    width: 100vw;
    display: flex;
    align-items: flex-end;
    position: fixed;
    background-color: #ffffff;
    position: -webkit-sticky;
    position: sticky;
    z-index: 1001;
    top: 0;
    left: 0;
    box-shadow: 0 1px 2px rgb(0 0 0 / 6%);
    

}
.Black{
    color: black;
}
.Contact{
    width: 100px;
}
.HeaderLeft{
    height: 70%;
    width: 50%;
}
.logoImg{
    height: 50% auto;
    width: 50% ;
}
.HeaderCenter{
    height: 70%;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.HeaderRight{
    height: 70%;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.EnquireButton{

    height: 3rem;
    width: 12rem;
    border-radius: 22px;
    background-color: #596aff;
    color: white;
    border: none;
}
.EnquireButton:hover{
    background-color: #ffffff;
    color: black;
}
.Container{
    height: 80%;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-top: 1.5rem;
    margin-bottom: 5rem;   
    display: flex;
    flex-direction: column;
}

.Upper{
    height: 100px;
    color: #596aff;
}

.Lower{
    width: 100%;
    display: flex;

    flex-wrap: wrap;
    justify-content: space-around;

}
.CardMain{

    height: 310px;
    justify-content: center;
    width: 400px;
    margin-top:8rem;
    position: relative;
    display: flex;
    background-color: #a8b8ff; 
    border-radius: 5px;
    

}

.CardMain:hover{
    width: 410px;
    height: 320px;
    -webkit-transform: scale(1.10)  ;
            transform: scale(1.10)  ;
      transition: all 0.2s ease-in-out;

}
.allImg{
    height: 80%;
    width: 100%;

}

.CardPic{
    height: 250px;
    width: 350px;
    position: absolute;
    top: -25px;

}
.CardTag{
    height: 50px;
    width: 350px;
    position: absolute;
    top: 215px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Footer{
    height: 40%;
    width: 100%;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    background-color: #18263b;
}
.h3Rep{
    color: #66abff;
}
p{
    color: #f2f2f3;
}

.AboutUs , .OurInformation, .MyAccount , .Contact{
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
}
.UpperDiv{
    height: 400px;
    width: 60%;
    display: flex;
    flex-direction: row;
    padding-right: 6rem
    ;
}
.AboutUs{
    width: 60%;

    
}

.OurInformation{
    width: 60%;


}

.MyAccount{
    width: 60%;


}

.Contact{
    width: 60%;


}
.LowerDiv{
    height: 150px;
    width: 70%;
    display: flex;
}

.Timage{
    width: 150px;
    height: 70px;
    padding-top: 2rem;
    padding-left: 4rem;
    
}
